.new-expense__controls{
	display: flex;
	flex-wrap:wrap;
	grid-gap:  1rem;
	margin-bottom:1rem;
	text-align:left;
}



.new-expense__control label{
	font-weight: bold;
	margin-bottom:0.5rem;
	display:block;
	font-size:1.2rem;

}
.new-expense__control input{
	font:inherit;
	padding:0.5rem;
	border-radius: 6px;
	border:1px solid #ccc;
	width:100%;
	max-width:100%;

}
.new-expense__actions{
	text-align:right;
	/*background-color: white;*/
	padding-top: 1.2rem;
}